<template>
    <div>
        <div class="fixed-bottom-confirm-widget">
            <span class="base-btn-widget" @click="operate_edit" :state="edit_btn.state">{{edit_btn.text}}</span>
            &nbsp;&nbsp;&nbsp;&nbsp;
            <span class="base-btn-widget" @click="save_user" v-show="edit_btn.state === 'visied'">保存修改</span>
        </div>
        <div class="pad-h-2"></div>
        <div class="container grid">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget">个人中心</h2>
                    <hr class="seg-widget"/>

                    <!--侧边栏导航组件-->
                    <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                </div>
            </div>
            <div class="col-10" style="padding-bottom: 7rem">
                <div class="pannel-widget">
                    <h2 class="base-title-widget bt-w-blue" style="margin-bottom: 2rem">个人信息</h2>
                    <div class="grid">
                        <div class="col-2 pad-left-2">
                            <a href="####"><img class="round-img" ref="cover_img" :src="user_info.cover" @error="reset_cover" @click="upload"/></a>
                            <input type="file" ref="cover_file" @change="exe_upload" class="hide" />
                        </div>
                        <div class="col-8">
                            <div class="list-text-widget grid pad-w-2" :state="edit_state">
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>姓名：</span>
                                        <input type="text" class="lt-w-item-after" v-model="user_info.uname">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>性别：</span>
                                        <input type="text" class="lt-w-item-after" v-model="user_info.sex">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>年龄：</span>
                                        <input type="text" class="lt-w-item-after" v-model="user_info.age">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>电话：</span>
                                        <input type="text" class="lt-w-item-after" v-model="user_info.tel">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>邮箱：</span>
                                        <input type="text" class="lt-w-item-after" v-model="user_info.email">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>住址：</span>
                                        <input type="text" class="lt-w-item-after" v-model="user_info.addr">
                                    </div>
                                </div>
                                <div class="col-12">
                                    <div class="lt-w-item">
                                        <span>应聘岗位：</span>
                                        <input type="text" class="lt-w-item-after" v-model="user_info.jobs">
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="col-2 pcenter pad-right-2">
                            <!--<img :src="'../assets/user/' + user_info.uid + '/erweima.jpg'"/><br/><a>复制链接地址</a>-->
                        </div>
                    </div>
                    <hr class="seg-widget"/>
                    <div class="pad-h-2"></div>

                    <h2 class="base-title-widget bt-w-blue">教育背景</h2>
                    <div class="pad-w-2" v-if="user_info.edu.length > 0">
                        <div class="edit-widget" :state="edit_state" v-for="edu of user_info.edu">
                            <div class="e-w-top"><span class="e-w-del icon-shanchu" @click="del_item('edu_' + edu.id)"></span></div>
                            <div class="list-text-widget grid" :state="edit_state">
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>入学时间：</span>
                                        <input type="text" class="lt-w-item-after" v-model="edu.start_time" placeholder="YYYY-MM-DD">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>毕业时间：</span>
                                        <input type="text" class="lt-w-item-after" v-model="edu.end_time" placeholder="YYYY-MM-DD">
                                    </div>
                                </div>
                                <div class="col-4">
                                    <div class="lt-w-item">
                                        <span>院校/机构：</span>
                                        <input type="text" class="lt-w-item-after" v-model="edu.chool">
                                    </div>
                                </div>
                                <div class="col-4">
                                    <div class="lt-w-item">
                                        <span>专业：</span>
                                        <input type="text" class="lt-w-item-after" v-model="edu.major">
                                    </div>
                                </div>
                                <div class="col-4">
                                    <div class="lt-w-item">
                                        <span>文凭：</span>
                                        <input type="text" class="lt-w-item-after" v-model="edu.diploma">
                                    </div>
                                </div>
                                <div class="col-12">
                                    <div class="lt-w-item">
                                        <span>奖项荣誉：</span>
                                        <textarea class="lt-w-item-textarea" v-model="edu.desc"></textarea>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="no-result-widget" v-else>
                        <span class="icon-nodata nr-w-ico"></span>
                        <span class="nr-w-text">暂无教育背景</span>
                    </div>
                    <div class="record-btn-widget" :state="edit_state">
                        <span class="rb-w-add icon-tianjia" @click="add_item('edu')"></span>
                    </div>
                    <hr class="seg-widget"/>

                    <div class="pad-h-2"></div>
                    <h2 class="base-title-widget bt-w-blue">专业技能</h2>
                    <div class="pad-w-2" v-if="user_info.skill.length > 0">
                        <div class="edit-widget" :state="edit_state" v-for="skill of user_info.skill">
                            <div class="e-w-top"><span class="e-w-del icon-shanchu" @click="del_item('skill_' + skill.id)"></span></div>
                            <div class="list-text-widget grid" :state="edit_state">
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>相关技能：</span>
                                        <input type="text" class="lt-w-item-after" v-model="skill.name">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>掌握程度：</span>
                                        <span class="lt-w-item-after">
                                            <select v-model="skill.degree" class="lt-w-item-after-select">
                                                <option value="know">了解</option>
                                                <option value="shuxi">熟悉</option>
                                                <option value="skilled">熟练</option>
                                                <option value="master">精通</option>
                                            </select>
                                        </span>
                                    </div>
                                </div>
                                <div class="col-12">
                                    <div class="lt-w-item">
                                        <span>技能介绍：</span>
                                        <textarea class="lt-w-item-textarea" v-model="skill.desc"></textarea>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="no-result-widget" v-else>
                        <span class="icon-nodata nr-w-ico"></span>
                        <span class="nr-w-text">暂无专业技能</span>
                    </div>
                    <div class="record-btn-widget" :state="edit_state">
                        <span class="rb-w-add icon-tianjia" @click="add_item('skill')"></span>
                    </div>
                    <hr class="seg-widget"/>

                    <div class="pad-h-2"></div>
                    <h2 class="base-title-widget bt-w-blue">项目经验</h2>
                    <div class="pad-w-2" v-if="user_info.pro.length > 0">
                        <div class="edit-widget" :state="edit_state" v-for="pro of user_info.pro">
                            <div class="e-w-top"><span class="e-w-del icon-shanchu" @click="del_item('pro_' + pro.id)"></span></div>
                            <div class="list-text-widget grid" :state="edit_state">
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>项目名：</span>
                                        <input type="text" class="lt-w-item-after" v-model="pro.name">
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="lt-w-item">
                                        <span>完成时间：</span>
                                        <input type="text" class="lt-w-item-after" v-model="pro.date_time" placeholder="YYYY-MM-DD">
                                    </div>
                                </div>
                                <div class="col-12">
                                    <div class="lt-w-item">
                                        <span>项目链接：</span>
                                        <input type="text" class="lt-w-item-after" v-model="pro.link"
                                               v-if="edit_state == 'edit'">
                                        <a class="lt-w-item-after" :href="pro.link" v-else-if="pro.link">{{pro.link}}
                                            (点击链接可跳转)</a>
                                        <span class="lt-w-item-after" v-else>{{pro.link}}暂无链接</span>
                                    </div>
                                </div>
                                <div class="col-12">
                                    <div class="lt-w-item">
                                        <span>项目介绍：</span>
                                        <textarea class="lt-w-item-textarea" v-model="pro.desc"></textarea>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="no-result-widget" v-else>
                        <span class="icon-nodata nr-w-ico"></span>
                        <span class="nr-w-text">暂无项目经验</span>
                    </div>
                    <div class="record-btn-widget" :state="edit_state">
                        <span class="rb-w-add icon-tianjia" @click="add_item('pro')"></span>
                    </div>
                    <hr class="seg-widget"/>

                    <div class="pad-h-2"></div>
                    <h2 class="base-title-widget bt-w-blue">自我评价</h2>
                    <div class="pad-w-2">
                        <div class="list-text-widget" :state="edit_state">
                            <div class="lt-w-item">
                                <span>相关介绍：</span>
                                <textarea class="lt-w-item-textarea" v-model="user_info.desc"></textarea>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";

    export default {
        name: "user_home_resume",
        data() {
            return {

                /*aside_nav_widget*/
                aside_nav_data: [
                    {
                        name: "我的成绩",
                        type: "route",
                        link: "/user_home",
                    },
                    {
                        name: "我的空间",
                        type: "route",
                        link: "/user_home_center",
                    },
                    {
                        name: "我的简历",
                        type: "route",
                        act: "act",
                        link: "/user_home_resume",
                    },
                ],

                /*用户信息*/
                user_info: {
                    uid: "",
                    cover: "",
                    uname: "",
                    sex: "",
                    age: "",
                    tel: "",
                    email: "",
                    addr: "",
                    jobs: "",
                    desc: "",
                    edu: [],
                    skill: [],
                    pro: []
                },

                /*教育背景*/
                edu: {
                    start_time: "",
                    end_time: "",
                    chool: "",
                    major: "",
                    diploma: "",
                    desc: "",
                    del: []
                },

                /*专业技能*/
                skill: {
                    name: "",
                    degree: "",
                    desc: "",
                    del: []
                },

                /*项目经验*/
                pro: {
                    name: "",
                    link: "",
                    date_time: "",
                    desc: "",
                    del: []
                },

                /*编辑简历按钮文字*/
                edit_btn: {
                    text: "编辑简历",
                    state: "normal"
                },

                /*编辑模式*/
                edit_state: "normal",

                /*FormData*/
                formdata: new FormData(),

                /*弹窗*/
                confirm: null,

            }
        },
        methods: {

            /*重置头像*/
            reset_cover(e){
                e.currentTarget.src = "data:image/jpeg;base64,";
            },

            /*编辑简历*/
            operate_edit() {
                if (this.edit_btn.text === "编辑简历") {
                    this.edit_state = "edit";
                    this.edit_btn.text = "取消编辑";
                    this.edit_btn.state = "visied";
                } else {
                    this.edit_state = "normal";
                    this.edit_btn.text = "编辑简历";
                    this.edit_btn.state = "normal";
                }
            },

            /*删除记录*/
            del_item(id) {
                let id_arr = id.split("_"),
                    record = this.user_info[id_arr[0]],
                    _id = id_arr[1];
                record.forEach((el, i) => {
                    if (el.id == _id) {
                        record.splice(i, 1);
                        if(_id.search("E:") !== 0){//记录实际删除数据
                            this[id_arr[0]].del.push(_id);
                        }
                        return;
                    }
                })
            },

            /*新增记录*/
            add_item(type) {
                let temp = this[type],
                    new_data = {};
                for (let pro in temp) {
                    if(pro !== "del") new_data[pro] = "";
                }
                new_data.id = "E:" + Date.now();
                new_data.op_type = "add";
                this.user_info[type].push(new_data);
            },

            /*点击上传头像*/
            upload() {
                this.$refs.cover_file.click();
            },

            /*执行上传*/
            exe_upload(e){
                let file = e.target.files[0],
                    file_reader = new FileReader(),
                    type = file.type.split("/")[0];
                if(type === "image"){
                    this.formdata.append('img', file);
                    file_reader.onload = (e) => {
                        this.$refs.cover_img.src = e.target.result; //显示预览图
                    };
                    file_reader.readAsDataURL(file);
                }else{
                    this.confirm.toast("上传头像文件有误!", 2000, "notic");
                }
            },

            /*保存修改*/
            save_user() {
                let u = this.user_info,
                    formdata = this.formdata;
                formdata.append('name', u.uname);
                formdata.append('age', u.age);
                formdata.append('gender', u.sex);
                formdata.append('address', u.addr);
                formdata.append('mobile', u.tel);
                formdata.append('email', u.email);
                formdata.append('expected_work', u.jobs);
                formdata.append('assessment', u.desc);
                this.$axios({
                    method: 'put',
                    url: "userinfo/" + this.$store.state.user.uid +"/",
                    data: formdata,
                    headers: {
                        'Content-Type': 'multipart/form-data'
                    }
                }).then(res => {
                    if(res.statusText === "OK"){//个人信息&自我评价修改
                        console.log("个人信息及自我评价修改 success!");
                        this.$store.commit("update_uname", u.uname);
                        this.confirm.toast("简历修改成功", 2000, "success");
                        this.operate_edit();

                        /*编辑教学背景*/
                        this.exe_edit(this.user_info, "edu", {
                            Study_time: "T:start_time",
                            end_time: "T:end_time",
                            school_name: "chool",
                            major: "major",
                            education: "diploma",
                            honor: "desc"
                        }, "教学背景", "education");

                        /*编辑专业技能*/
                        this.exe_edit(this.user_info, "skill", {
                            skill_name: "name",
                            skill_level: "degree",
                            skill_introduce: "desc"
                        }, "专业技能", "skillvi");

                        /*编辑项目经验*/
                        this.exe_edit(this.user_info, "pro", {
                            project_name: "name",
                            technology: "link",
                            project_function: "desc",
                            create: "desc",
                            time_slot: "T:date_time"
                        }, "项目经验", "project");

                    }
                })
            },

            /*执行编辑操作（新增、修改、删除）*/
            exe_edit(datas, prop, record, name, api){
                datas[prop].forEach(d => {
                    let op_data = {},
                        record_arr;
                    for(let pro in record){
                        record_arr = record[pro].split("T:")
                        if(record_arr.length > 1){//时间格式
                            op_data[pro] = this.record_time_format(d[record_arr[1]]);
                        }else{
                            op_data[pro] = d[record[pro]];
                        }
                    }
                    op_data["user"] = this.$store.state.user.uid;
                    if(d.op_type === "add"){//新增数据
                        this.$axios.post(api + "/", op_data).then(r => {
                            if(r.status == 200) console.log("新增" + name + " success!");
                        })
                    } else{//修改数据
                        this.$axios.put(api + "/" + d.id + "/", op_data).then(r => {
                            if(r.status == 200) console.log("修改" + name + " success!");
                        })
                    }
                })

                /*删除数据*/
                this[prop].del.forEach(id => {
                    this.$axios.delete(api + "/" + id + "/", {
                        id: id
                    }).then(r => {
                        console.log("删除" + name + " success!");
                    })
                })

            },

            /*记录时间格式化*/
            record_time_format(time){
                return time + "T01:00";
            },

            /*显示时间格式化*/
            show_time_format(time){
                return time.split("T")[0];
            }

        },
        components: {
            "aside-nav-widget": AsideNav,
        },
        mounted() {
            this.confirm = this.$B.widgets.Confirm;
            this.$axios.all([
                this.$axios.get("skillvi/"),
                this.$axios.get("education/"),
                this.$axios.get("project/"),
                this.$axios.get("userinfo/"),
            ]).then(this.$axios.spread((r1, r2, r3, r4) => {

                /*专业技能*/
                let skill_data = r1.data;
                if (skill_data.length > 0) {
                    this.user_info.skill = this.$B.data_convert(skill_data, {
                        id: "1",
                        name: "skill_name",
                        degree: "skill_level",
                        desc: "skill_introduce"
                    })
                }

                /*教育背景*/
                let edu_data = r2.data;
                if (edu_data.length > 0) {
                    this.user_info.edu = this.$B.data_convert(edu_data, {
                        id: "1",
                        start_time: (d)=>{
                            return this.show_time_format(d.Study_time)
                        },
                        end_time: (d)=>{
                            return this.show_time_format(d.end_time)
                        },
                        chool: "school_name",
                        major: "1",
                        diploma: "education",
                        desc: "honor"
                    })
                }

                /*项目经验*/
                let pro_data = r3.data;
                if (pro_data.length > 0) {
                    this.user_info.pro = this.$B.data_convert(pro_data, {
                        id: "1",
                        name: "project_name",
                        link: "technology",
                        date_time: (d)=>{
                            return this.show_time_format(d.time_slot)
                        },
                        desc: "project_function"
                    })
                }

                /*个人信息*/
                let user_data = r4.data[0];
                this.user_info.uid = user_data.id;
                this.user_info.cover = user_data.img;
                this.user_info.uname = user_data.name;
                this.user_info.sex = user_data.gender;
                this.user_info.age = user_data.age;
                this.user_info.tel = user_data.mobile;
                this.user_info.email = user_data.email;
                this.user_info.addr = user_data.address;
                this.user_info.jobs = user_data.expected_work;
                this.user_info.desc = user_data.assessment;

            }));
        }
    }
</script>

<style scoped>
    @import "../../assets/css/pannel-widget.css";
    @import "../../assets/css/seg-widget.css";
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/base-btn-widget.css";
    @import "../../assets/css/list-text-widget.css";
    @import "../../assets/css/edit-widget.css";
    @import "../../assets/css/fixed-bottom-confirm-widget.css";
    @import "../../assets/css/no-result-widget.css";
    @import "../../assets/css/record-btn-widget.css";
</style>


