<template>
    <div style="padding-bottom: 5rem;">
        <div class="pad-h-2"></div>
        <div class="container grid">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget">个人中心</h2>
                    <hr class="seg-widget"/>

                    <!--侧边栏导航组件-->
                    <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                </div>
            </div>
            <div class="col-10">
                <div class="pannel-widget">
                    <h3 class="base-title-widget bt-w-blue">成绩单</h3>
                    <hr class="seg-widget"/>
                    <div class="pad-h-1"></div>

                    <!--基本列表文本-->
                    <!--<div class="list-text-widget grid pad-w-2">
                        <div class="col-6">
                            <div class="lt-w-item">
                                <span>班级排名：</span>
                                <span class="lt-w-item-after">{{report.class_rank}}</span>
                            </div>
                        </div>
                        <div class="col-6">
                            <div class="lt-w-item">
                                <span>月排名：</span>
                                <span class="lt-w-item-after">{{report.month_rank}}</span>
                            </div>
                        </div>
                        <div class="col-6">
                            <div class="lt-w-item">
                                <span>总排名：</span>
                                <span class="lt-w-item-after">{{report.total_rank}}</span>
                            </div>
                        </div>
                        <div class="col-6">
                            <div class="lt-w-item">
                                <span>预计完成时间：</span>
                                <span class="lt-w-item-after">{{report.end_time}}</span>
                            </div>
                        </div>
                    </div>-->
                    <!--临时-->
                    <div id="Echarts" style="height: 400px; padding: 0 2rem;"></div>

                </div>
                <div class="pad-h-1"></div>
                <div class="pannel-widget">
                    <h3 class="base-title-widget bt-w-blue">任务线</h3>
                    <hr class="seg-widget"/>

                    <!--任务线-->
                    <div class="task-line-widget" v-if="task_line.length > 0">
                        <ul class="tl-w-list">
                            <li class="tl-w-item" :state="task.act" v-for="task of task_line">
                                <span class="tl-w-date">{{task.date}}</span>
                                <span>{{task.task}}</span>
                            </li>
                        </ul>
                    </div>
                    <div class="no-result-widget" v-else>
                        <span class="icon-nodata nr-w-ico"></span>
                        <span class="nr-w-text">暂无任务完成</span>
                    </div>

                    <!--<div class="pad-left-2"><a @click="show_more_task">查看更多</a></div>-->
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";

    /*测试数据*/
    var report = {//【data:成绩单】
        class_rank: "第 1 名",
        month_rank: "第 15 名",
        total_rank: "第 3 名（2018-10-10）",
        progress: "JAVA反射",
        end_time: "2018-10-14"
    }, task_line = [//【data:任务线】
        {
            date: "10月9日",
            task: "完成 Hadoop 基础入门作业提交"
        },
        {
            date: "10月9日",
            task: "完成 Java 课程学习"
        },
        {
            date: "10月8日",
            task: "完成 Hbase 阶段学习",
            act: "act",
        },
        {
            date: "10月7日",
            task: "完成 HTML 课程学习"
        },
        {
            date: "10月6日",
            task: "完成 CSS 课程学习"
        },
    ];

    export default {
        name: "user_home",
        data() {
            return {

                /*aside_nav_widget*/
                aside_nav_data: [
                    {
                        name: "我的成绩",
                        act: "act",
                        type: "route",
                        link: "/user_home",
                    },
                    {
                        name: "我的空间",
                        type: "route",
                        link: "/user_home_center",
                    },
                    {
                        name: "我的简历",
                        type: "route",
                        link: "/user_home_resume",
                    },
                ],

                /*成绩单*/
                report: {},

                /*任务线*/
                task_line: [],

                /*临时：任务进度*/
                option: {
                    title : {
                        text: 'HTML基础 章节: 完成度',
                        subtext: 'Web技术基础',
                        x:'center'
                    },
                    tooltip : {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                        data: ['已完成','未完成']
                    },
                    series : [
                        {
                            name: 'HTML基础 章节: 完成度',
                            type: 'pie',
                            radius : '55%',
                            center: ['50%', '60%'],
                            data:[
                                {value:0, name:'已完成', itemStyle: {color:'#21f8b6'}},
                                {value:4, name:'未完成', itemStyle: {color:'#ff7e00'}}
                            ],
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                }

            }
        },
        components: {
            "aside-nav-widget": AsideNav,
        },
        mounted() {
            this.$axios.all([
                this.$axios.get("day_rank/"),
                this.$axios.get("user_mission/")
            ]).then(this.$axios.spread((r1, r2) => {

                /*排名信息*/
                let ranks = r1.data;
                ranks.forEach(rank => {
                    if(rank.user.id == this.$store.state.user.uid){
                        this.report = {
                            class_rank: rank.class_rankings,
                            month_rank: rank.monthly_rankings,
                            total_rank: rank.total_ranking,
                            end_time: rank.estimated_time
                        }
                    }
                })

                /*任务线信息*/
                let task = r2.data;
                this.task_line = this.$B.data_convert(task, {
                    date: (data) => {
                        return data.complete_time.replace("T", " ");
                    },
                    task: (data) => {
                        return data.chapter.chapter_name + " -> " + data.mission.name;
                    },
                    act: (data) => {
                        return data.task_end ? "act" : "";
                    }
                })

                /*临时请求*/
                let myChart = echarts.init(document.getElementById('Echarts'));
                this.$axios.get("task/").then(res=>{
                    let count = 0,
                        ids = [];
                    res.data.forEach(r=>{
                        if(r.task_end == true){
                            if(ids.indexOf(r.mission.id) === -1){
                                ids.push(r.mission.id);
                                count++;
                            }
                        }
                    })
                    this.option.series[0].data[0].value = count;
                    this.option.series[0].data[1].value = 4 - count;
                    myChart.setOption(this.option);
                })

            }))
        }
    }
</script>

<style scoped>
    @import "../../assets/css/pannel-widget.css";
    @import "../../assets/css/seg-widget.css";
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/list-text-widget.css";
    @import "../../assets/css/task-line-widget.css";
    @import "../../assets/css/no-result-widget.css";
</style>


