<template>
    <div>
        <matte-video-widget :video="matte_video"></matte-video-widget>
        <div class="pad-h-2"></div>
        <div class="container grid" style="padding-bottom: 5rem">
            <div class="col-2 pad-right-2">
                <div class="pannel-widget">
                    <h2 class="base-title-widget">个人中心</h2>
                    <hr class="seg-widget"/>

                    <!--侧边栏导航组件-->
                    <aside-nav-widget :init_data="aside_nav_data"></aside-nav-widget>

                </div>
            </div>
            <div class="col-10">
                <div class="pannel-widget">

                    <!--文件列表组件-->
                    <div class="base-table-widget" v-if="works.length > 0">
                        <table class="bt-w-table">
                            <thead class="bt-w-table">
                            <tr class="bt-w-title">
                                <td class="bt-w-title-td">章节</td>
                                <td class="bt-w-title-td pcenter">任务</td>
                                <td class="bt-w-title-td pcenter">提交时间</td>
                                <td class="bt-w-title-td pcenter">配图</td>
                                <td class="bt-w-title-td pcenter">视频附件</td>
                                <td class="bt-w-title-td pcenter">压缩包附件</td>
                                <!--<td class="bt-w-title-td pcenter">博客</td>-->
                            </tr>
                            </thead>
                            <tbody>
                            <tr class="bt-w-tr" v-for="work of works">
                                <td class="bt-w-td">{{work.chapter.chapter_name}}</td>
                                <td class="bt-w-td pcenter">{{work.mission.name}}</td>
                                <td class="bt-w-td pcenter">{{time_format(work.submit_time)}}</td>
                                <td class="bt-w-td pcenter">
                                    <a target="_blank" :href="work.image" v-if="work.image"><img :src="work.image" class="bt-w-img" /></a>
                                    <span class="bt-w-no-data" v-else>无配图</span>
                                </td>
                                <td class="bt-w-td pcenter">
                                    <a href="####" @click="open_video_matte(work.chapter.chapter_name + ' -> ' + work.mission.name, work.video_file)" v-if="work.video_file"><i class="icon-bofang bt-w-ico-wenjian"></i>播放</a>
                                    <span class="bt-w-no-data" v-else>无附件</span>
                                </td>
                                <td class="bt-w-td pcenter">
                                    <a :href="work.file" download v-if="work.file"><i class="icon-xiazai bt-w-ico-wenjian"></i>下载</a>
                                    <span class="bt-w-no-data" v-else>无附件</span>
                                </td>
                                <!--<td class="bt-w-td pcenter">
                                    <a href="####"><i class="icon-bianji bt-w-ico-wenjian"></i><span>编辑</span></a>
                                </td>-->
                            </tr>
                            </tbody>
                        </table>
                    </div>
                    <div class="no-result-widget" v-else>
                        <span class="icon-nodata nr-w-ico"></span>
                        <span class="nr-w-text">暂无作业提交</span>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import AsideNav from "@/components/widget/aside-nav-widget";
    import MatteVideo from "@/components/widget/matte-video-widget";

    export default {
        name: "user_home_center",
        data() {
            return {

                /*aside_nav_widget*/
                aside_nav_data: [
                    {
                        name: "我的成绩",
                        type: "route",
                        link: "/user_home",
                    },
                    {
                        name: "我的空间",
                        act: "act",
                        type: "route",
                        link: "/user_home_center",
                    },
                    {
                        name: "我的简历",
                        type: "route",
                        link: "/user_home_resume",
                    },
                ],

                /*提交作业数据*/
                works: [],

                /*遮罩视频*/
                matte_video: {
                    state: "hide",
                    title: "",
                    video: ""
                }

            }
        },
        methods: {

            /*格式化时间*/
            time_format(time){
                let time_arr = time.substr(2).replace("T", " ").replace(/-/g, "/").split(":");
                time_arr.pop();
                return time_arr.join(":");
            },

            /*开启视频层*/
            open_video_matte(title, video){
                this.matte_video = {
                    state: "show",
                    title: title,
                    video: video
                };
            }

        },
        components: {
            "aside-nav-widget": AsideNav,
            "matte-video-widget": MatteVideo
        },
        mounted() {
            this.$axios.get("task/")
                .then(res => {
                    this.works = res.data;
                })
        }
    }
</script>

<style scoped>
    @import "../../assets/css/pannel-widget.css";
    @import "../../assets/css/seg-widget.css";
    @import "../../assets/css/base-title-widget.css";
    @import "../../assets/css/base-table-widget.css";
    @import "../../assets/css/base-btn-widget.css";
    @import "../../assets/css/no-result-widget.css";
</style>


