<template>
    <div class="complet-notic-widget" ref="widget"></div>
</template>

<script>
    export default {
        name: "complet-notic-widget",
        props: ["pos"],
        methods: {

            /*定位图形*/
            set_pos(){
                for(let pro in this.pos){
                    this.$refs.widget.style[pro] = this.pos[pro]+"px";
                }
            }

        },
        mounted(){
            this.set_pos();
        }
    }
</script>

<style scoped>
    @import "../../assets/css/complet-notic-widget.css";
</style>


