<template>
    <div class="container" style="padding-bottom: 5rem">
        <h2 class="main-title-widget">
            <router-link class="mt-w-link"
                         :to="'/learn_path?cid=' + cur_sub_class.course_id + '&cname=' + cur_sub_class.name">
                {{cur_sub_class.name}}
            </router-link>
            <span class="mt-w-text">{{task_detail.name}}</span>
        </h2>
        <div class="pannel-widget">
            <complet-notic-widget v-if="btn_state.state === 'visied'" :pos="{'left':340, 'top':10}"></complet-notic-widget>
            <div class="pad-h-1"></div>
            <div class="grid">
                <div class="col-4 pad-w-2">
                    <div class="video-widget">
                        <video controls :src="task_detail.chapter_video"></video>
                    </div>
                </div>
                <div class="col-8 pad-w-2">
                    <h2>{{task_detail.name}}
                        <span style="margin-left: 2rem">
                            <a :href="task_detail.task_file" download v-if="task_detail.task_file"><i class="icon-fujian"></i> 教学资源下载</a>
                            <a href="####" v-else @click="no_file_notic"><i class="icon-fujian"></i> 教学资源下载</a>
                        </span>
                    </h2>
                    <div class="basic-article-widget">
                        <h3 class="ba-w-title">本课目标：</h3>
                        <div style="font-size: 1.3rem" v-html="task_detail.chapter_target"></div>
                    </div>
                    <div>
                        <span v-show="btn_state.state === 'visied'">任务已完成&nbsp;&nbsp;&nbsp;&nbsp;</span>
                        <router-link class="base-btn-widget" :to="btn_state.next_task" v-if="btn_state.next_task">下一任务</router-link>
                        <span class="base-btn-widget" @click="send_work(true)" :state="btn_state.state" v-else>{{btn_state.text}}</span>
                    </div>
                    <div class="pright">
                        <router-link :to="'/library?search=' + task_detail.name">> 去图书馆搜索“{{task_detail.name}}”</router-link>
                    </div>
                </div>
            </div>
        </div>
        <div class="pannel-widget" style="margin-top: 1rem">
            <tab-widget :tabs="tabs"></tab-widget>
        </div>
        <div v-if="task_detail.id" style="margin-top: 1rem">
            <div class="pannel-widget grid">

                <!--作业任务-->
                <div class="basic-article-widget col-8">
                    <div class="ba-w-p pad-w-2">
                        <p v-html="task_detail.chapter_task"></p>
                        <div class="ba-w-p-imgs">
                            <img class="ba-w-p-img" :src="img.image" v-for="img of task_detail.task_image"/>
                            <p>
                                <span state="visied" class="base-btn-widget" @click="upload">
                                <input type="file" multiple v-show="false" @change="exe_upload" ref="work_uploader">上传作业</span><span class="p-w-sub-text">* 支持：视频、图片、压缩包 文件，各类型文件单次上传仅限1个 *</span>
                            </p>
                        </div>
                    </div>
                    <h3 class="base-title-widget">作业提交记录</h3>
                    <hr class="seg-widget"/>
                    <div class="task-line-widget" v-if="upload_record.length > 0">
                        <ul class="tl-w-list">
                            <li :state="record.task_end === true?'act':''" class="tl-w-item" v-for="(record, i) of upload_record">
                                <span class="tl-w-date">{{record.submit_time.replace("T", " ")}}</span>
                                <span>【第 {{i+1}} 次提交】: {{record.task_status == 2?(record.task_end === true?'通过':'未过'): '未审核'}}</span>
                                <a class="tl-w-btn" @click="check_comments(record.id, i+1)">查看点评</a>
                            </li>
                        </ul>
                    </div>
                    <div class="no-result-widget" v-else>
                        <span class="icon-nodata nr-w-ico"></span>
                        <span class="nr-w-text">暂无作业上传</span>
                    </div>
                </div>

                <!--作业点评-->
                <div class="col-4">
                    <h3 class="base-title-widget bt-w-blue">作业点评<span class="bt-w-right" v-if="cur_comments.turn != 0">第【{{cur_comments.turn}}】次提交</span></h3>
                    <hr class="seg-widget"/>
                    <div v-if="cur_comments.comments.length > 0">
                        <div class="pad-w-2" v-for="(msg, i) of cur_comments.comments">
                            <div class="basic-article-widget">
                                <p class="ba-w-p" v-html="msg.text"></p>
                                <p class="ba-w-small-p pright">
                                    <span class="text-strong">{{msg.date_time}}</span>
                                    <head-widget :uname="msg.teacher"></head-widget>
                                </p>
                            </div>
                            <hr class="seg-widget" state="max_line" v-if="cur_comments.comments.length-1 > i"/>
                        </div>
                    </div>
                    <div class="no-result-widget" v-else>
                        <span class="icon-nodata nr-w-ico"></span>
                        <span class="nr-w-text">暂无点评信息</span>
                    </div>
                </div>

            </div>
        </div>
        <div class="pannel-widget no-result-widget" style="margin-top: 1rem" v-else>
            <span class="icon-nodata nr-w-ico"></span>
            <span class="nr-w-text">暂无内容</span>
        </div>
    </div>
</template>

<script>
    import $B from "@/lib/Btx";
    import Tab from "@/components/widget/tab-widget";
    import Head from "@/components/widget/head-widget";
    import CompletNotic from "@/components/widget/complet-notic-widget";

    export default {
        name: "learn_detail",
        data() {
            let vue = this;
            return {

                /*课程介绍*/
                cur_sub_class: {},

                /*按钮状态*/
                btn_state: {
                    text: "完成任务",
                    state: "normal",
                    next_task: ""
                },

                /*课程详情*/
                task_detail: {},

                /*上传记录*/
                upload_record: [],

                /*全部任务点评*/
                comments: [],

                /*当前任务点评*/
                cur_comments: {
                    turn: 0,
                    comments: []
                },

                /*tab_widget*/
                tabs: [
                    {
                        name: "任务",
                        state: "act"
                    },
                    {
                        name: "练习",
                        state: "",
                        callback() {
                            vue.$router.push("/question?cid=" + vue.task_detail.id);
                        }
                    }
                ],

                /*弹窗*/
                confirm: null,

            }
        },
        methods: {

            /*初始化任务*/
            init_task(){
                this.$axios.all([
                    this.$axios.get("chapter/" + this.$route.query.sub_c_id + "/"),
                    this.$axios.get("task/"),
                    this.$axios.get("evaluation/")
                ]).then(this.$axios.spread((r1, r2, r3) => {
                    let cdata = r1.data;

                    /*章节介绍*/
                    this.cur_sub_class = {
                        name: cdata.course_name.name,
                        course_id: cdata.course_name.id,
                        chapter_id: cdata.id,
                    }

                    /*当前任务*/
                    cdata.tasks.forEach(task=>{
                        if(task.id == this.$route.query.task_id) this.task_detail = task;
                    })

                    /*已提交作业记录*/
                    r2.data.forEach(d=>{
                        if(d.mission.id == this.task_detail.id){
                            this.upload_record.push(d);
                        }
                    })
                    this.send_work();//通过提交作业判断当前任务是否已完成

                    /*评论消息列表*/
                    let msgs = r3.data;
                    msgs.forEach(msg => {
                        if (msg.user === this.$store.state.user.uid && this.task_detail.id === msg.mission) {
                            this.comments.push({
                                text: msg.data,
                                teacher: msg.teacher.name,
                                date_time: msg.evaluation_time.replace("T", " "),
                                user_mission_id: msg.user_mission_id
                            });
                        }
                    });
                    this.comments.reverse();

                }))
            },

            /*完成任务*/
            send_work(if_click) {
                let complete = false;
                this.upload_record.forEach(record => {
                    if(record.task_end == true){
                        this.btn_state.state = "visied";
                        complete = true;

                        /*获取下一任务*/
                        this.$axios.get("chapter/").then(res => {
                            res.data.forEach(chapter => {
                                if(chapter.id === this.cur_sub_class.chapter_id){//确认章节
                                    chapter.tasks.forEach((task, i) => {
                                        if(task.id === this.task_detail.id){//确认任务
                                            let next_task = chapter.tasks[i+1];
                                            if(next_task){//有下一任务
                                                this.btn_state.next_task = '/learn_detail?sub_c_id=' + chapter.id + '&task_id=' + next_task.id;
                                            }else{//该章节已完成
                                                this.btn_state.text = "本章节已完成";
                                            }
                                        }
                                    })
                                }
                            })
                        })

                        return;
                    }
                })
                if(if_click === true && complete === false){
                    this.confirm.toast("作业未提交，或老师未审核通过", 2000, "notic");
                }
            },

            /*选择任务或练习*/
            sel_tab(e) {
                $B.select("p-w-tab", e.target);
            },

            /*点击上传作业*/
            upload() {
                this.$refs.work_uploader.click();
            },

            /*上传执行*/
            exe_upload(e) {
                let files = e.target.files,
                    formdata = new FormData(),
                    task = this.task_detail,
                    file_count = 0;
                this.confirm.load("文件上传中");

                /*按类型添加文件*/
                for(let i=0; i<files.length; i++){
                    let file = files[i],
                        type = file.type.split("/")[0],
                        name_type = file.name.split("\.")[1];
                    if(type === "image"){//图片类型
                        formdata.append('image', file);
                        file_count++;
                    }else if(type === "video"){//视频类型
                        formdata.append('video_file', file);
                        file_count++;
                    }else if(name_type === "rar" || name_type === "zip"){//压缩文件
                        formdata.append('file', file);
                        file_count++;
                    }
                }

                if(file_count > 0){
                    formdata.append('data_info', task.chapter_target);
                    formdata.append('task_end', false);
                    formdata.append('submit_time', this.$utils.gen_date_time());
                    formdata.append('complete_time', "2018-01-01T01:00:00");
                    formdata.append('chapter', task.chapter_name);
                    formdata.append('mission', task.id);
                    this.$axios({
                        method: 'post',
                        url: "task/",
                        data: formdata,
                        headers: {
                            'Content-Type': 'multipart/form-data'
                        }
                    }).then(res => {
                        this.upload_record.push(res.data);
                        this.confirm.load("文件上传成功", "end");
                    })
                }else{
                    this.confirm.toast("无正确格式上传文件!", 2000, "notic");
                }
            },

            /*提示无上传附件*/
            no_file_notic(){
                this.confirm.toast("暂无教学资源附件", 2000);
            },

            /*查看点评*/
            check_comments(id, n){
                this.cur_comments.comments.length = 0;
                this.cur_comments.turn = n;
                this.comments.forEach(comment => {
                    if(id == comment.user_mission_id) this.cur_comments.comments.push(comment);
                })
            }

        },
        mounted() {
            this.confirm = this.$B.widgets.Confirm;
            this.init_task();
        },
        watch:{
            $route(){
                this.$router.go(0);
            }
        },
        components: {
            "tab-widget": Tab,
            "head-widget": Head,
            "complet-notic-widget": CompletNotic
        }
    }
</script>

<style scoped>
    @import "../assets/css/main-title-widget.css";
    @import "../assets/css/pannel-widget.css";
    @import "../assets/css/video-widget.css";
    @import "../assets/css/base-btn-widget.css";
    @import "../assets/css/basic-article-widget.css";
    @import "../assets/css/base-title-widget.css";
    @import "../assets/css/no-result-widget.css";
    @import "../assets/css/seg-widget.css";
    @import "../assets/css/task-line-widget.css";
</style>


