<template>
    <div class="container">
        <h2 class="main-title-widget">
            <router-link class="mt-w-link" to="/coffee/student">返回同学论坛</router-link>
        </h2>
        <div class="grid">
            <div class="col-7">

                <!--文章正文-->
                <div class="pannel-widget" style="margin-bottom: 1rem;">
                    <h3 class="base-title-widget">
                        <span>{{cur_article.title}}</span><br/>
                        <span class="bt-w-sub">{{cur_article.date_time}}&nbsp;&nbsp;|&nbsp;&nbsp;{{cur_article.author}}</span>
                    </h3>
                    <hr class="seg-widget"/>
                    <div class="basic-article-widget">
                        <article class="ba-w-blog" v-html="cur_article.text"></article>
                    </div>
                </div>

                <!--相关评论-->
                <div class="pannel-widget">
                    <h3 class="base-title-widget bt-w-blue">相关评论</h3>
                    <div class="pcenter">

                        <!--评论编辑器-->
                        <div class="pad-w-2" v-show="comment_btn.state === 'normal'">
                            <div class="pad-h-1"></div>
                            <Vueditor ref="editor"></Vueditor>
                        </div>

                        <div class="pad-h-2"></div>
                        <span class="base-btn-widget" @click="send_discuss"
                              :state="comment_btn.state" v-text="comment_btn.text"></span>
                        <div class="pad-h-1"></div>
                    </div>
                    <hr class="seg-widget"/>
                    <div v-if="comments.length > 0">
                        <div class="pad-w-2" v-for="(comment, i) of comments">
                            <div class="basic-article-widget">
                                <p class="ba-w-p" v-html="comment.text"></p>
                                <p class="ba-w-small-p pright">
                                    <span class="text-strong">{{comment.date_time}}</span>
                                    <head-widget :uname="comment.uname"></head-widget>
                                </p>
                            </div>
                            <hr class="seg-widget" state="max_line" v-if="comments.length-1 > i"/>
                        </div>
                    </div>
                    <div class="no-result-widget" v-else>
                        <span class="icon-nodata nr-w-ico"></span>
                        <span class="nr-w-text">暂无评论信息</span>
                    </div>
                </div>

            </div>

            <!--相关文章-->
            <div class="col-5 pad-left-2">
                <div class="pannel-widget">
                    <h3 class="base-title-widget">相关文章</h3>
                    <hr class="seg-widget"/>
                    <div class="pad-h-2"></div>
                    <div class="list-text-widget grid pad-w-2">
                        <div class="lt-w-item col-12" v-for="msg of msges.slice(0, 10)">
                            <router-link :to="{path:'/blog', query:{article_id:msg.article_id}}">
                                {{msg.title}}
                            </router-link>
                            <span class="lt-w-item-after">{{msg.active}}</span>
                        </div>
                    </div>
                    <div class="pad-h-1"></div>
                </div>
            </div>

        </div>

        <div class="pad-h-5"></div>
    </div>
</template>

<script>
    import Head from "@/components/widget/head-widget";
    import Vue from 'vue'
    import Vuex from 'vuex'
    import Vueditor from 'vueditor'
    import 'vueditor/dist/style/vueditor.min.css' //引入相关样式文件

    Vue.use(Vuex);
    Vue.use(Vueditor, {
        spellcheck: false,
        toolbar: [ //工具栏内容
            'fontSize', 'foreColor', 'backColor', '|', 'bold', 'italic', 'emoji'
        ],
    });

    export default {
        name: "blog",
        data() {
            return {

                /*最新论坛消息*/
                msges: [],

                /*当前文章*/
                cur_article: {},

                /*相关评论*/
                comments: [],

                /*评论按钮状态*/
                comment_btn: {
                    state: "visied",
                    text: "评论",
                },

            }
        },
        components: {
            "head-widget": Head,
        },
        watch: {
            $route(to) {
                this.init_article();
            }
        },
        methods: {

            /*初始化页面*/
            init_article() {
                this.$axios.all([
                    this.$axios.get("discuss_msg/"),
                    this.$axios.get("discuss_msg/" + this.$route.query.article_id + "/"),
                ]).then(this.$axios.spread((r1, r2) => {

                    /*最新论坛消息*/
                    let msges = r1.data;
                    this.msges = this.$B.data_convert(msges, {
                        article_id: "id",
                        title: "title",
                        active: (data) => {
                            return data.send_time.replace("T", " ");
                        }
                    })
                    this.msges.reverse();

                    /*当前文章*/
                    let cur_article = r2.data;
                    this.cur_article = {
                        article_id: cur_article.id,
                        title: cur_article.title,
                        author: cur_article.user.name,
                        date_time: cur_article.send_time.replace("T", " "),
                        text: cur_article.content
                    }

                    /*相关评论*/
                    this.comments = this.$B.data_convert(cur_article.discuss_replay, {
                        comment_id: "id",
                        text: "content",
                        uname: (data) => {
                            return data.user.name;
                        },
                        date_time: (data) => {
                            return data.send_time.replace("T", " ");
                        }
                    })
                    this.comments.reverse();
                }))
            },

            /*发布评论*/
            send_discuss() {
                if (this.comment_btn.state === "visied") {//开始评论
                    this.comment_btn.state = "normal";
                    this.comment_btn.text = "发布评论";
                } else if (this.comment_btn.state === "normal") {//发布评论
                    let cont = this.$refs.editor.getContent();
                    if(cont){
                        this.$axios.post("discuss_replay/", {
                            send_time: this.$utils.gen_date_time(),
                            content: cont,
                            discuss: this.cur_article.article_id,
                            user: this.$store.state.user.uid,
                        }).then(res => {
                            if(res.statusText === "Created"){//新增评论成功
                                let new_c = res.data;
                                this.comments.unshift({
                                    comment_id: new_c.id,
                                    text: new_c.content,
                                    uname: this.$store.state.user.uname,
                                    date_time: new_c.send_time.replace("T", " ")
                                })
                                this.comment_btn.state = "visied";
                                this.comment_btn.text = "评论";
                                this.$refs.editor.setContent("");
                            }
                        })
                    }
                }

            }

        },
        mounted() {
            this.init_article();
        }
    }
</script>

<style>
    @import "../assets/css/pannel-widget.css";
    @import "../assets/css/base-title-widget.css";
    @import "../assets/css/seg-widget.css";
    @import "../assets/css/basic-article-widget.css";
    @import "../assets/css/list-text-widget.css";
    @import "../assets/css/base-btn-widget.css";
    @import "../assets/css/main-title-widget.css";
    @import "../assets/css/no-result-widget.css";
</style>


